<template>
  <div v-for="value in teams" :key="value" :style="{height: myHeight1+'px'}">
    <div v-for="item in value" :key="item">
      <div v-for="myItem in item.matches" :key="myItem"  class="my-4">

        <div class="flex flex-row align-center justify-center">
          <span class="text-slate-600 text-center font-thin" :style="{fontSize: sizeText+'px'}">{{myItem.long_date_text}}</span>
          <span class="text-slate-600 text-center font-thin mx-2" :style="{fontSize: sizeText+'px'}">{{myItem.week}}</span>
        </div>
        <div class="flex flex-row align-center justify-between w-full">

          <div class="flex flex-row" :style="{width: widthTeam+'%'}">
            <span class="text-black font-thin ml-1" :style="{fontSize:sizeText+'px'}">{{myItem.home_team.name_fa}}</span>
            <img @error="notShow" :src="`${myItem.home_team.logo}`" alt="" class="w-7 h-7 rounded-full">
          </div>
          <div class="flex flex-col space-y-1">
            <div class="flex flex-row">
              <span class="text-slate-800 text-center font-thin text-sm">{{myItem.home_team_score}}</span>
              <span class="text-slate-800 text-center font-thin text-sm"> - </span>
              <span class="text-slate-800 text-center font-thin text-sm">{{myItem.away_team_score}}</span>
            </div>
            <div><span class="text-slate-800 text-center font-thin text-sm">{{myItem.status}}</span></div>
            <div><span class="text-slate-800 text-center font-thin" :style="{fontSize: sizeText+'px'}">ویدیو ها</span></div>
          </div>
          <div class="flex flex-row align-center" :style="{width: widthTeam+'%'}">
            <img @error="notShow" :src="`${myItem.away_team.logo}`" alt="" class="w-7 h-7 rounded-full">
            <span class="text-black font-thin text-sm ml-1" :style="{fontSize: sizeText+'px'}">{{myItem.away_team.name_fa}}</span>
          </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {ref} from "vue";

export default {
  name: "RankPlays",
  props:{
    teams:{
      type:Array,
      required:true,
      default:null
    }
  },
  setup(){
    const widthTeam=ref(30)
    const sizeText=ref(12)
    const myHeight1=ref(535)
    const notFound=ref('https://footballi.net/assets/shared-application/images/placeholders/ic_player_default.png')
    function notShow(event) {
      event.target.src = notFound.value
    }
    return{
      notFound,
      notShow,sizeText,widthTeam,myHeight1
    }
  }
}
</script>

